import React from "react";
// @Mui
import { Box, Container, Stack } from "@mui/material";
import Logo from "../Logo";

// ---------------------------------------------------------------------

function Header() {
  return (
    <Box sx={{ bgcolor: "background.paper", py: 2 }}>
      <Container>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Box sx={{ width: 200 }}>
            <Logo />
          </Box>
          {/* Search */}
        </Stack>
      </Container>
    </Box>
  );
}

export default Header;
